
<template>
  <li class="node-tree" @click.stop="handleTitleClick(node)">
    <div class="menuItem"  v-bind:class="{ hoverOver: depth>0 }">
      <span class="menuItemInner" v-if="!node.hidden && depth>0" v-bind:class="{ isSelected: node.isSelected }">
        <span v-if="node.children && node.children.length && depth>0 && !node.showChildren" class="expand" @click.stop="toggleChildren">+</span>
        <span v-else-if="node.children && node.children.length && depth>0 && node.showChildren" class="collapse" @click.stop="toggleChildren">-</span>
        <span class=" title">{{ node.label }}</span>
      </span>
    </div>
    <div v-if="node.showChildren">
      <div :style="indent" v-if="node.children && node.children.length">
        <node v-for="child in node.children" :node="child" :depth="depth+1" v-bind:key="child" :handle-title-click="handleTitleClick"></node>
      </div>
    </div>
  </li>
</template>

<script>
//import store from '../store'
export default {
  emits: {
    'handleExpandClick' (node) {
        if (node) {
          node.showChildren=!node.showChildren;
          //debugger // eslint-disable-line no-debugger
        }
        return true
    },
  },
  
  name: "node",
  setup () {
    //debugger // eslint-disable-line no-debugger
    //store is NOT available yet!
  },
  props: {
    node: Object,
    depth: Number,
    handleTitleClick: Function,
  },
  data() {
    //return {showChildren: this.node.expanded, currentItem:undefined }
    return {currentItem:undefined }
  },
  computed: {
    indent() {
      return { transform: `translate(${this.depth * 20}px)` }
    },
  },
  methods: {
    setCurrentPage() {
      //debugger // eslint-disable-line no-debugger
      console.log("setCurrentPage: " + this.node.label)
      this.$store.dispatch('navMenu/setCurrentPage', {obj: this.node})
      //const store = useStore();
      //store.dispatch('app/showMessageScreen', {truefalse: false})
      //this.$emit("set-current-page", this.node)
    },
    toggleChildren() {
      //this.handleTitleClick(this.node)
      this.$emit('handleExpandClick', this.node)
    },
  },
};
</script>

<style scoped>
.isSelected {
  display: inline-block;
  font-weight: bolder;
}
.expand {
  width: 1rem;
  display: inline-block;
}
.collapse {
  width: 1rem;
  display: inline-block;
}
.inline {
  display: inline-block;
}
.hoverOver:hover {
  background-color: antiquewhite;
}
.menuItem  {
  padding:0.3rem;
}
.menuItemInner {
  display:flex;
}
